.settings span {
  cursor: pointer;
}

.settings span select {
  cursor: pointer;
}

.settings input, .settings label {
  cursor: pointer;
}

.sf-menu.settings {
  float: right;
  position: relative;
  top: -26px;
}

.sf-menu.settings li {
  background-color: #333;
  font-size: 15px;
}

.sf-menu.settings li a {
  color: #ddd;
}

.sf-menu.settings li a:hover {
  text-decoration: none;
}
