a.alert-details-trigger {
  text-decoration: none;
  color: #222;
  font-weight: normal;
  font-size: 16px;
  &:hover {
    text-decoration: underline;
  }
}

.alert-details-modal {
  background: #fff;
  padding: 35px;
  border-radius: 2px;
  max-width: 934px;
  float: left;
  left: 50%;
  top: 50%;
  height: 900px;
  transform: translate(-50%, -50%);
  position: relative;

  .alert-details-modal-inner {
    overflow-y: scroll;
    overflow-x: hidden;
    padding: 0;
    height: 865px;
    overflow: auto;
    position: absolute;

    img {
      padding-right: 20px;
      max-width: 930px;
      object-fit: scale-down;
      cursor: initial;
    }
  }
}

li.alertbox {
  cursor: pointer;
  -webkit-transition: filter 300ms ease;
  -ms-transition: filter 300ms ease;
  transition: filter 300ms ease;

  &:hover {
    filter: brightness(118%);
  }
}

.alert-full-description  {
  p {
    margin-bottom: 20px;
    font-size: 24px;
  }
}

hr {
  margin-bottom: 20px;
}
