#configure-commute h2 {
  position: relative;
  right: 10px;
  text-transform: uppercase;
  font-size: 40px;
}

.configure-commute-explainer {
  display: inline-block;
  height: 200px;
  width: 420px;
  position: relative;
  top: -140px;
  left: 30px;
  border: 1px solid #ccc;
  padding: 30px 20px 0 330px;
  border-radius: 2px;
  position: relative;
  left: -14px;
  z-index: -100;
}

.configure-commute-explainer .explainer-calm {
  margin-top: 30px;
}

.configure-commute-explainer p {
  font-size: 20px;
  line-height: 32px;
}
