/* MEDIA QUERIES FOR MOBILE */
/*  iPhone X  */
@media only screen and (device-width: 375px) and (device-height: 812px) and (-webkit-device-pixel-ratio: 3) {
  body {
    -webkit-appearance: none;
    /* padding: 50px; */
  }
}

/*  iPhone 7/8  */
@media only screen and (device-width: 375px) and (device-height: 667px) and (-webkit-device-pixel-ratio: 2) {
  body {
    -webkit-appearance: none;
    /* padding: 50px; */
  }
}

/*  iPhone 6+/6s+/7+/8+  */
@media only screen and (device-width: 414px) and (device-height: 736px) and (-webkit-device-pixel-ratio: 3) {
  body {
    -webkit-appearance: none;
  }
  header {
    max-height: 100%;
    height: 40px;
  }
  header .header-wrapper {
    padding-top: 20px;
    width: 900px;
  }
  header .header-wrapper .switch {
    margin-left: 0;
    top: 30px;
  }
  .overlay {
    -webkit-filter: blur(8px);
    -moz-filter: blur(8px);
    -o-filter: blur(8px);
    -ms-filter: blur(8px);
    filter: blur(8px);
    opacity: 0.7;
  }
  ul.trainData-am, ul.trainData-pm {
    width: 900px;
    clear: both;
    overflow: hidden;
  }
  ul.trainData-am li.arrival-time, ul.trainData-pm li.arrival-time {
    font-size: 2.2em;
  }
  ul.trainData-am li.arrival-time.performant-transition-box, ul.trainData-pm li.arrival-time.performant-transition-box {
    width: 860px;
  }
  ul.trainData-am li.arrival-time canvas, ul.trainData-pm li.arrival-time canvas {
    height: 50px;
    width: 50px;
  }
  ul.trainData-am li.arrival-time:not(.blank-arrival), ul.trainData-pm li.arrival-time:not(.blank-arrival) {
    top: -94px;
  }
  span.brand-callout {
    font-size: 26px;
  }
  #configure-commute h2 {
    font-size: 30px;
    margin-bottom: 40px;
  }
  .system-alerts h2 {
    font-size: 30px;
  }
  h2.am-commute-station-label, h2.am-commute-station-label {
    font-size: 30px;
  }
  .content-wrapper {
    width: 900px;
  }
  .blank-arrival {
    width: 818px;
  }
  li.arrival-time:not(.blank-arrival) {
    position: relative;
    top: -125px;
  }
  .boarded-status {
    width: 900px;
  }
  h2 {
    font-size: 1em;
  }
  .alertbox b {
    font-size: 16px;
  }
  .alertbox p {
    font-size: 20px;
  }
  .station-setting {
    position: relative;
  }
  .awesomplete > ul > li {
    font-size: 30px;
    width: 600px;
  }
  .footer-callout {
    font-size: 12px;
    margin-bottom: 20px;
  }
  ul.footer li {
    font-size: 30px;
  }
  span.sf-with-ul {
    top: 20px;
  }
  .sf-menu.settings {
    top: -37px;
  }
  li.route-data {
    font-size: 1.5em;
  }
}

@media only screen and (device-width: 414px) and (device-height: 736px) and (-webkit-device-pixel-ratio=3) {
  body {
    padding: 50px;
  }
}
