.weather-data-all {
  width: 100%;
  padding-bottom: 20px;
  height: 90px;
}

.weather-data-container.current {
  display: inline-block;
  float: right;
}

.weather-data-container.current .weather-data {
  left: 510px;
}

.weather-data-container.current .weather-data .weather-show-days-adjust {
  float: left;
  font-size: 35px;
}

.weather-data-container.current .weather-data .weather-show-days-adjust .weather-adjust-more {
  position: relative;
  top: 10px;
  left: 14px;
}

.weather-data-container.current .weather-data .weather-show-days-adjust .weather-adjust-less {
  position: relative;
  top: 54px;
  left: -15px;
}

.weather-data-container.current .weather-data .weather-show-days-adjust .transition-adjust-btn {
  opacity: 0.55;
}

.weather-data-container.current .weather-data .weather-show-days-adjust .transition-adjust-btn:hover:not(.disabled-adjust) {
  cursor: pointer;
  opacity: 1;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.weather-data-container.current .weather-data .weather-show-days-adjust .disabled-adjust:hover {
  cursor: not-allowed;
}

.weather-data-container.current .weather-data .weather-sparkline-box {
  float: left;
  height: 95px;
  width: 150px;
  font-size: 20px;
  text-align: center;
  border-left: 1px solid #999;
  border-top: 1px solid #999;
  border-bottom: 1px solid #999;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

.weather-data-container.current .weather-data .weather-sparkline-box #temp-trends {
  width: 150px;
  border-bottom: 1px solid #999;
}

.weather-data-container.current .weather-data .weather-sparkline-box .precipitation-trends {
  position: relative;
  top: -90px;
}

.weather-data-container.current .weather-data .weather-sparkline-box .min-max-temp-detail {
  font-weight: bold;
}

.weather-data-container.current .weather-data .weather-sparkline-box span {
  font-size: 14px;
  position: relative;
}

.weather-data-container.current .weather-data .weather-sparkline-box .detail-precipitation {
  position: relative;
  top: -74px;
  left: -50px;
}

.weather-data-container.current .weather-data .weather-sparkline-box .forecast-timeframe {
  float: left;
  left: 10px;
  top: -16px;
  font-weight: bold;
  width: 30px;
}

.weather-data-container.current .weather-data .weather-sparkline-box .forecast-max {
  float: right;
  margin-right: 6px;
}

.weather-data-container.current .weather-data .weather-sparkline-box .forecast-min {
  float: right;
  top: 18px;
  left: 64px;
}

.weather-data-container.current .weather-data .weather-details-box {
  border-top: 1px solid #999;
  border-left: 1px solid #999;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  float: left;
  height: 95px;
  width: 170px;
  font-size: 20px;
  text-align: center;
}

.weather-data-container.current .weather-data .weather-details-box .weather-wind-box {
  display: inline-block;
  height: 50px;
  top: 5px;
}

.weather-data-container.current .weather-data .weather-details-box .weather-wind-box img.wind-icon {
  height: 30px;
  width: 30px;
  margin-right: 6px;
  margin-top: 7px;
}

.weather-data-container.current .weather-data .weather-details-box .weather-humidity-box {
  display: inline-block;
  height: 33px;
  top: 15px;
  background-color: #555;
  width: 170px;
  color: white;
  padding-top: 11px;
  font-weight: 100;
}

.weather-data-container.current .weather-data .weather-details-box .weather-humidity-box .humidity {
  font-size: 16px;
}

.weather-data-container.current .weather-data .weather-sunrise-sunset-box {
  height: 93px;
  width: 110px;
  border-bottom: 1px solid #999;
  border-top: 1px solid #999;
  float: left;
}

.weather-data-container.current .weather-data .weather-sunrise-sunset-box .description-container {
  display: inline-block;
  width: 110px;
  height: 50px;
  text-align: center;
  font-size: 22px;
  background-color: #00c3ff;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

.weather-data-container.current .weather-data .weather-sunrise-sunset-box .description-container .icon-description {
  line-height: 50px;
  color: white;
}

.weather-data-container.current .weather-data .weather-sunrise-sunset-box .sunrise-sunset-container {
  display: inline-block;
  height: 44px;
  text-align: center;
  background-color: #00c3ff;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  padding-top: 5px;
  background: #555;
  color: white;
}

.weather-data-container.current .weather-data .weather-sunrise-sunset-box .sunrise-sunset-container .weather-sunrise {
  top: 51px;
}

.weather-data-container.current .weather-data .weather-sunrise-sunset-box .sunrise-sunset-container .weather-sunset {
  top: 55px;
}

.weather-data-container.current .weather-data .weather-temp-box {
  height: 65px;
  width: 105px;
  float: right;
  background-color: #555;
  color: white;
  padding: 15px;
}

.weather-data-container.current .weather-data .weather-temp-box .weather-icon {
  position: relative;
  left: 60px;
  top: -34px;
}

.weather-data-container.current .weather-data .weather-temp-box .weather-temp {
  font-size: 40px;
  font-weight: bold;
  display: inline;
  left: 0;
  top: 0;
}

.weather-data-container.current .weather-data .weather-temp-box .weather-temp .unit-indicator {
  font-weight: 100;
}

.weather-data-container.current .weather-data .weather-temp-box .weather-city {
  text-align: center;
  font-size: 16px;
  font-weight: lighter;
  left: 10px;
}
