
ul.trainData-am, ul.trainData-pm {
  margin-top: 15px;

  li.arrival-time {
    padding: 20px;
    margin: 5px 0;
    box-shadow: 1px 1px 2px 0 #666;
    cursor: pointer;

    &.performant-transition-box {
      position: relative;
      display: inline-block;
      width: 960px;
      background-color: #fff;
      box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
      -webkit-transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
      transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);

      &::after {
        content: '';
        position: absolute;
        z-index: -1;

        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
        opacity: 0;
        -webkit-transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
        transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
      }

      &:hover {
        -webkit-transform: scale(1.01, 1.01);
        transform: scale(1.01, 1.01);
        &::after {
          opacity: 1;
        }
      }
    }

    &:not(.blank-arrival) {
      position: relative;
      top: -90px;
    }

    &:hover {
      .line-detail {
        text-decoration: underline;
      }
    }

    span.countdown {
      font-size: 50px;
      float: right;
      font-family: 'IBM Plex Sans', 'Lucida Console', 'Monaco', monospace;
      letter-spacing: 0.1em;
    }

    .line-detail {
      font-size: 20px;
      margin: 0 0 -15px 0;
      height: 50px;
    }
    .station-arrival {
      position: relative;
      top: -10px;
      img.airport-icon {
        position: relative;
        top: 16px;
        padding: 10px 0 10px 10px;
      }
    }
  }
}
