a.alert-details-trigger {
  text-decoration: none;
  color: #222;
  font-weight: normal;
  font-size: 16px;
}

a.alert-details-trigger:hover {
  text-decoration: underline;
}

.alert-details-modal {
  background: #fff;
  padding: 35px;
  border-radius: 2px;
  max-width: 934px;
  float: left;
  left: 50%;
  top: 50%;
  height: 900px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  position: relative;
}

.alert-details-modal .alert-details-modal-inner {
  overflow-y: scroll;
  overflow-x: hidden;
  padding: 0;
  height: 865px;
  overflow: auto;
  position: absolute;
}

.alert-details-modal .alert-details-modal-inner img {
  padding-right: 20px;
  max-width: 930px;
  -o-object-fit: scale-down;
     object-fit: scale-down;
  cursor: initial;
}

li.alertbox {
  cursor: pointer;
  -webkit-transition: filter 300ms ease;
  -webkit-transition: -webkit-filter 300ms ease;
  transition: -webkit-filter 300ms ease;
  transition: filter 300ms ease;
  transition: filter 300ms ease, -webkit-filter 300ms ease;
}

li.alertbox:hover {
  -webkit-filter: brightness(118%);
          filter: brightness(118%);
}

.alert-full-description p {
  margin-bottom: 20px;
  font-size: 24px;
}

hr {
  margin-bottom: 20px;
}
