ul.trainData-am, ul.trainData-pm {
  margin-top: 15px;
}

ul.trainData-am li.arrival-time, ul.trainData-pm li.arrival-time {
  padding: 20px;
  margin: 5px 0;
  -webkit-box-shadow: 1px 1px 2px 0 #666;
          box-shadow: 1px 1px 2px 0 #666;
  cursor: pointer;
}

ul.trainData-am li.arrival-time.performant-transition-box, ul.trainData-pm li.arrival-time.performant-transition-box {
  position: relative;
  display: inline-block;
  width: 960px;
  background-color: #fff;
  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
          box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  -webkit-transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}

ul.trainData-am li.arrival-time.performant-transition-box::after, ul.trainData-pm li.arrival-time.performant-transition-box::after {
  content: '';
  position: absolute;
  z-index: -1;
  -webkit-box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
          box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  opacity: 0;
  -webkit-transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}

ul.trainData-am li.arrival-time.performant-transition-box:hover, ul.trainData-pm li.arrival-time.performant-transition-box:hover {
  -webkit-transform: scale(1.01, 1.01);
  transform: scale(1.01, 1.01);
}

ul.trainData-am li.arrival-time.performant-transition-box:hover::after, ul.trainData-pm li.arrival-time.performant-transition-box:hover::after {
  opacity: 1;
}

ul.trainData-am li.arrival-time:not(.blank-arrival), ul.trainData-pm li.arrival-time:not(.blank-arrival) {
  position: relative;
  top: -90px;
}

ul.trainData-am li.arrival-time:hover .line-detail, ul.trainData-pm li.arrival-time:hover .line-detail {
  text-decoration: underline;
}

ul.trainData-am li.arrival-time span.countdown, ul.trainData-pm li.arrival-time span.countdown {
  font-size: 50px;
  float: right;
  font-family: 'IBM Plex Sans', 'Lucida Console', 'Monaco', monospace;
  letter-spacing: 0.1em;
}

ul.trainData-am li.arrival-time .line-detail, ul.trainData-pm li.arrival-time .line-detail {
  font-size: 20px;
  margin: 0 0 -15px 0;
  height: 50px;
}

ul.trainData-am li.arrival-time .station-arrival, ul.trainData-pm li.arrival-time .station-arrival {
  position: relative;
  top: -10px;
}

ul.trainData-am li.arrival-time .station-arrival img.airport-icon, ul.trainData-pm li.arrival-time .station-arrival img.airport-icon {
  position: relative;
  top: 16px;
  padding: 10px 0 10px 10px;
}
