input {
  background: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  border-radius: 0px;
}

/* MESSAGES */
#offline-warning {
  margin-bottom: 250px;
}

.station-setting-container {
  clear: both;
  position: relative;
}

.station-setting-container.setup-only {
  bottom: 60px;
}

a:link {
  text-decoration: none;
  font-weight: bold;
}

li {
  font-size: 40px;
  line-height: 40px;
  color: #eee;
  list-style: none;
}

.trainData-am, .trainData-pm {
  width: 1000px;
  clear: both;
  margin-top: 20px;
}

.trainData-am .accessibility-icon, .trainData-pm .accessibility-icon {
  top: 10px;
  position: relative;
}

.trainData-am .blank-arrival, .trainData-pm .blank-arrival {
  width: 956px;
}

.trainData-am li.blank-arrival-am, .trainData-am li.blank-arrival-pm, .trainData-pm li.blank-arrival-am, .trainData-pm li.blank-arrival-pm {
  background-color: #999;
  position: relative;
  z-index: -10;
  top: 0px;
  display: block;
}

.select-filter {
  font-size: 25px;
  min-width: 260px;
}

.select-filter.am-train-filter, .select-filter.pm-train-filter {
  float: right;
  padding-bottom: 15px;
}

.station-setting {
  line-height: 36px;
  font-size: 25px;
  position: relative;
}

.station-setting.departure {
  margin-top: 0;
  margin-bottom: 10px;
  margin-left: 10px;
  width: 250px;
  right: 0;
}

.station-setting.destination {
  float: right;
  width: 300px;
  left: 18px;
}

.compass {
  /* background: url(http://i.imgur.com/44nyA.jpg); */
  background-size: cover;
  padding-left: 10px;
  top: 6px;
  position: relative;
}
